<template>
    <div class="service-detail">
        <el-form ref="form" label-width="110px" class="w800">
            <div class="w550">
                <h3>基本设置</h3>
                <el-form-item label="企业名称:">
		            <div>{{service.customer_name}}</div>
                </el-form-item>
                <el-form-item label="社会信用代码:">
	                <div>{{service.social_credit_code}}</div>
                </el-form-item>
                <el-form-item label="联系人:">
	                <div>{{service.contact_name}}</div>
                </el-form-item>
                <el-form-item label="行业类型:">
	                <div>{{service.industry_type_id}} {{service.industry_subtype_id}}</div>
                </el-form-item>
                <el-form-item label="所在地:">
                    <!-- todo 城市选择控件未确定 -->
                    <div>{{service.province_id}} {{service.city_id}}</div>
                    
                </el-form-item>
                <el-form-item label="管理员帐号:">
	                <div>{{service.admin_id}}</div>
                </el-form-item>
                <el-form-item label="电力用户编码:">
                    <div>{{service.power_customer_no}}</div>
                </el-form-item>
                <el-form-item label="水力用户编码:">
	                <div>{{service.water_customer_no}}</div>
                </el-form-item>
                <el-form-item label="燃气用户编码:">
	                <div>{{service.gas_customer_no}}</div>
                </el-form-item>
                <el-form-item label="通信服务编码:">
	                <div>{{service.comm_customer_no}}</div>
                </el-form-item>
            </div>
            <el-form-item label="营业执照:" class="bb">
	            <img :src="service.business_licence_img">
            </el-form-item>
            <div class="w550">
                <h3>服务设置</h3>
                <el-form-item label="有效时间:">
                	<div>{{service.start_time}} - {{service.end_time}}</div>
                </el-form-item>
                <el-form-item label="开启服务:">

	                <span v-for="(item,i) in service.enabled_services">{{item}}</span>
                </el-form-item>
                <el-form-item label="开启行业:">
                	<span v-for="(item,i) in service.enabled_industries">{{item}}</span>
                </el-form-item>
            </div>
        </el-form>
    </div>
</template>
<script>
import * as constants from './constants.js'
export default {
    data() {
        return {
        	service: constants.initService()
        }
    },
    computed: {
    	id (){
    		return this.$route.params.serviceId
    	}
    },
    methods: {
    	loadServiceDetaild (){
    		// this.$api.service.getServiceById(this.id).then(data => {
    		// 	this.service = data
    		// })
    	}
    },
    created (){
    	this.loadServiceDetaild()
    }
}
</script>
<style lang="scss">
	.service-detail{
		.w800{
			width: 800px;
		}
	}

	
	
</style>


